$transition: .3s ease-in-out;

@media screen and (max-width: 991px) {
  .page-menu {
    .menu-list {
      width: 100%;
      max-width: 244px;
      min-height: unset;
      padding: 18px 4px;
      display: flex;
      flex-direction: column;
      position: fixed;
      top: 0;
      left: 0;
      bottom: 0;
      z-index: 10;
      background-color: #79818C;
      opacity: 0;
      visibility: hidden;
      transform: translateX(-100%);
      pointer-events: none;
      transition: transform $transition, opacity $transition, visibility $transition;

      &.open {
        opacity: 1;
        visibility: visible;
        transform: translateX(0);
        pointer-events: auto;
      }

      &:before {
        display: none;
      }

      .item-wrap {
        flex: 1;
        max-height: 100%;
        padding: 0 17px;
        overflow: auto;
      }
    }

  }
  .toolbar-menu {
    padding-left: 0;
  }
  .content-wrap {
    padding: 12px 0 0 0;
  }
  .review-item .text-wrap p {
    max-width: 565px;
  }

  .login-form {
    padding: 40px 15px 40px 0;
  }
}

@media (max-width: 767px) {
  .user-data p {
    display: none;
  }
  .user-data .info {
    padding-left: 0;
    border-left: none;
  }
  .user-data .info button {
    margin-left: 10px;
  }
  .login-form {
    width: 100%;
    min-height: unset;
    padding: 40px 0;
  }
  .login-bg {
    width: 100%;
    &:before {
      content: '';
      display: block;
      padding-bottom: 80%;
    }
    .bg-wrap {
      width: 101vw;
      left: 50%;
      transform: translateX(-50%);
    }
  }
}

@media (max-width: 575px) {
  .main-links {
    width: 100%;

    ul {
      justify-content: space-around;

      li {
        padding-right: 15px;

        &:last-child {
          padding-right: 0;
        }
      }
    }
  }
  .page-intro .user-data {
    display: none;
  }
}